.photo__box {
  cursor: pointer;
  width: 400px;
  height: 400px;
  margin-left: -75px;
  margin-top: -75px;
  position: absolute;
  top: 35%;
  right: 0px;
  transform-style: preserve-3d;
  transform: rotateY(0deg);
  -webkit-transform: rotateY(0deg);
  -moz-transform: rotateY(0deg);
  -ms-transform: rotateY(0deg);
  -o-transform: rotateY(0deg);

  .photo-back {
    display: block;
    border-radius: 50%;
    width: 400px;
    height: 400px;
    transform: translateZ(-10px);
    -webkit-transform: translateZ(-10px);
    -moz-transform: translateZ(-10px);
    -ms-transform: translateZ(-10px);
    -o-transform: translateZ(-10px);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
  }

  .photo-middle {
    display: block;
    border-radius: 50%;
    // border: 2px solid rgb(0 95 171);
    background: rgb(0 95 171);
    width: 400px;
    height: 400px;
    position: absolute;
    top: 0;
  }

  .photo-front {
    display: block;
    border-radius: 50%;
    width: 400px;
    height: 400px;
    position: absolute;
    top: 0;
    transform: translateZ(10px);
    -webkit-transform: translateZ(10px);
    -moz-transform: translateZ(10px);
    -ms-transform: translateZ(10px);
    -o-transform: translateZ(10px);
  }

  .photo-middle:nth-child(2) {
    transform: translateZ(-8px);
    -webkit-transform: translateZ(-8px);
    -moz-transform: translateZ(-8px);
    -ms-transform: translateZ(-8px);
    -o-transform: translateZ(-8px);
  }

  .photo-middle:nth-child(3) {
    transform: translateZ(-6px);
    -webkit-transform: translateZ(-6px);
    -moz-transform: translateZ(-6px);
    -ms-transform: translateZ(-6px);
    -o-transform: translateZ(-6px);
  }

  .photo-middle:nth-child(4) {
    transform: translateZ(-4px);
    -webkit-transform: translateZ(-4px);
    -moz-transform: translateZ(-4px);
    -ms-transform: translateZ(-4px);
    -o-transform: translateZ(-4px);
  }

  .photo-middle:nth-child(5) {
    transform: translateZ(-2px);
    -webkit-transform: translateZ(-2px);
    -moz-transform: translateZ(-2px);
    -ms-transform: translateZ(-2px);
    -o-transform: translateZ(-2px);
  }

  .photo-middle:nth-child(6) {
    transform: translateZ(0px);
    -webkit-transform: translateZ(0px);
    -moz-transform: translateZ(0px);
    -ms-transform: translateZ(0px);
    -o-transform: translateZ(0px);
  }

  .photo-middle:nth-child(7) {
    transform: translateZ(2px);
    -webkit-transform: translateZ(2px);
    -moz-transform: translateZ(2px);
    -ms-transform: translateZ(2px);
    -o-transform: translateZ(2px);
  }

  .photo-middle:nth-child(8) {
    transform: translateZ(4px);
    -webkit-transform: translateZ(4px);
    -moz-transform: translateZ(4px);
    -ms-transform: translateZ(4px);
    -o-transform: translateZ(4px);
  }

  .photo-middle:nth-child(9) {
    transform: translateZ(6px);
    -webkit-transform: translateZ(6px);
    -moz-transform: translateZ(6px);
    -ms-transform: translateZ(6px);
    -o-transform: translateZ(6px);
  }

  .photo-middle:nth-child(10) {
    transform: translateZ(8px);
    -webkit-transform: translateZ(8px);
    -moz-transform: translateZ(8px);
    -ms-transform: translateZ(8px);
    -o-transform: translateZ(8px);
  }
}

.moove-animate {
  animation: spin 5s cubic-bezier(0.9, -0.63, 0, 1.65);
  -webkit-animation: spin 5s cubic-bezier(0.9, -0.63, 0, 1.65);
}

.turn-animate {
  animation: turn 10s linear infinite;
  -webkit-animation: turn 10s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotateY(0deg);
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -ms-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
  }

  100% {
    transform: rotateY(360deg);
    -webkit-transform: rotateY(360deg);
    -moz-transform: rotateY(360deg);
    -ms-transform: rotateY(360deg);
    -o-transform: rotateY(360deg);
  }
}

@keyframes turn {
  0% {
    transform: rotateY(0deg);
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -ms-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
  }
  25% {
    transform: rotateY(30deg);
    -webkit-transform: rotateY(30deg);
    -moz-transform: rotateY(30deg);
    -ms-transform: rotateY(30deg);
    -o-transform: rotateY(30deg);
  }

  50% {
    transform: rotateY(0deg);
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -ms-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
  }

  75% {
    transform: rotateY(-30deg);
    -webkit-transform: rotateY(-30deg);
    -moz-transform: rotateY(-30deg);
    -ms-transform: rotateY(-30deg);
    -o-transform: rotateY(-30deg);
  }

  100% {
    transform: rotateY(0deg);
    -webkit-transform: rotateY(0deg);
    -moz-transform: rotateY(0deg);
    -ms-transform: rotateY(0deg);
    -o-transform: rotateY(0deg);
  }
}

