    width: 250px;
    height: 250px;
    margin-left: -75px;
    margin-top: -75px;
    top: 35%;
    right: 0px;

    .photo-back {
      width: 250px;
      height: 250px;
    }

    .photo-middle {
      width: 250px;
      height: 250px;
    }

    .photo-front {
      width: 250px;
      height: 250px;
    }
  }
}

